function main() {
  const form = document.querySelector('#loginForm')
  const title = document.querySelector('.mds-title')

  if (form.dataset.memberForm === 'signin') {
    title.innerHTML = 'Welcome back!'
  }
}

{
  if (document.readyState !== 'loading') {
    main()
  } else {
    document.addEventListener('DOMContentLoaded', function handleLoaded() {
      document.removeEventListener('DOMContentLoaded', handleLoaded)

      main()
    })
  }
}
